:root {
    --bumblebee: #ffff8c;
    --sombra: #3c5369;
}

#form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 80vw;
    height: 80vh;
    color: white;
}
.line {
    display: flex;
    width: 30vw;
    justify-content: space-between;
}

textarea {
    width: 40%;
    height: 10vw;
}
textarea:focus {
    background-color: var(--sombra);
    color: var(--bumblebee);
}
input {
    text-align: center;
}

input:focus {
    background-color: var(--sombra);
    color: var(--bumblebee);
}
#send {
    color: black;
}
#send-button {
    background-color: slategrey;
    color: black;
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;
    border: var(--bumblebee) 2px solid;
    border-radius: 5px;
    width: 80px;
    height: 40px;
}

#send-button:hover {
    background-color: var(--sombra);
    color: var(--bumblebee);
}