@import url('https://fonts.googleapis.com/css?family=Montserrat&display=swap');
:root {
  --bumblebee: #ffff8c;
  --sombra: #3c5369;
}
body {
  font-family: "Montserrat", sans-serif;
  text-align: center;
  background-color: var(--sombra);
}
.title {
    color: var(--bumblebee);
    font-size: 300%;
}
.subtitle {
    color: white;
    font-style: italic;
}
.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}